import React, { useState, useEffect } from "react";
import Avatar from "react-avatar";
import axios from "axios";
import { RerepleUploadDiv } from "../../../css/CommunityDetailElement.js";
import { useSelector } from "react-redux";

function RerepleUpload(props) {
  const [content, setContent] = useState("");
  const user = useSelector((state) => state.user);

  const submitHandler = (e) => {
    e.preventDefault();
    if (!content) {
      return alert("  .");
    }
    const body = {
      uid: user.userData.uid,
      repleInfo: props.Reple,
      content: content,
      postNum: props.Reple.postNum,
    };
    console.log("rereple", body);

    axios.post("/api/community/rerepleSubmit", body).then((response) => {
      if (response.data.success) {
        alert("  ");
        window.location.reload();
      } else {
        alert("  ");
      }
    });
  };

  return (
    <RerepleUploadDiv>
      <form action="post" onSubmit={submitHandler}>
        <div className="avatar">
          <Avatar
            src={user.userData.photoURL}
            size="50"
            round={true}
            style={{ border: "1px solid #c6c6c6" }}
          />
        </div>

        <p className="name">{user.userData.displayName}</p>

        <input
          type="text"
          placeholder=" "
          name="content"
          value={content}
          className="inputContent"
          onChange={(e) => setContent(e.currentTarget.value)}
        />

        <button
          type="button"
          className="cancel"
          onClick={() => {
            props.setrerepleUpload(false);
          }}
        >
          
        </button>

        <button type="submit" className="submit">
          
        </button>
      </form>
    </RerepleUploadDiv>
  );
}

export default RerepleUpload;
