import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import axios from "axios";

import FindingActorUploadFilter from "./Filter/FindingActorUploadFilter.js";
import FileUploadArea from "../../../utils/FileUploadArea.js";
import FileShowArea from "../../../utils/FileShowArea.js";
import PostBoardFilter from "./Filter/PostBoardFilter.js";
import LocationFilter from "./Filter/LocationFilter.js";
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx, css } from "@emotion/react";
import {
  FormDiv,
  BtnDiv,
  SubmitBtn,
  CancelBtn,
  DropZoneDiv,
} from "../../css/CommunityElement.js";

function UploadForm(props) {
  const [Title, setTitle] = useState("");
  const [Content, setContent] = useState("");
  const [Image, setImage] = useState([]);
  const [FilterCheck, setFilterCheck] = useState(false); //
  const [FilterElement, setFilterElement] = useState([]);
  const [SubCategory, setSubCategory] = useState("");

  const user = useSelector((state) => state.user);
  const submitHandler = (e) => {
    e.preventDefault();
    if (props.category === "" || props.category === "") {
      if (!FilterCheck) {
        return alert("  .");
      }
    }
    if (!Title || !Content) {
      return alert("  .");
    }
    const body = {
      uid: user.userData.uid,
      title: Title,
      content: Content,
      images: Image,
      category: props.category,
      filters: FilterElement,
      subCategory: SubCategory,
    };

    axios.post("/api/community/postSubmit", body).then((response) => {
      if (response.data.success) {
        alert("  ");
        props.history.push({
          pathname: "/community",
          state: { category: props.category },
        });
      } else {
        alert("  ");
      }
    });
  };

  const SwitchSubCategory = () => {
    switch (props.category) {
      case "":
        return <PostBoardFilter setSubCategory={setSubCategory} />;
      case "":
        return <></>;
      case "":
        return (
          <FindingActorUploadFilter
            FilterElement={FilterElement}
            setFilterElement={setFilterElement}
            setFilterCheck={setFilterCheck}
            FilterCheck={FilterCheck}
          />
        );
      case "":
        return <LocationFilter setSubCategory={setSubCategory} />;
      case "":
        return <></>;
    }
  };

  return (
    <>
      <FormDiv onSubmit={submitHandler}>
        <input
          name="title"
          className="title"
          placeholder=" "
          value={Title}
          onChange={(e) => setTitle(e.currentTarget.value)}
        />

        <div className="filterDiv">{SwitchSubCategory()}</div>

        <textarea
          name="content"
          className="content"
          placeholder={
            "            .\n          .\n\n        ,           .\n\n    \n      \n        "
          }
          value={Content}
          onChange={(e) => setContent(e.currentTarget.value)}
        ></textarea>

        <DropZoneDiv>
          <FileUploadArea Images={Image} setImages={setImage} />
          {Image[0] ? (
            <FileShowArea Images={Image} setImages={setImage} />
          ) : null}
        </DropZoneDiv>

        <BtnDiv>
          <CancelBtn
            onClick={() => {
              props.history.push("/community");
            }}
          >
            
          </CancelBtn>
          <button css={SubmitBtn} type="submit">
            
          </button>
        </BtnDiv>
      </FormDiv>
    </>
  );
}

export default withRouter(UploadForm);
